<template>
<main-layout :backgroundImage="`${$cloudinary}main.png`" backgroundOffset="50">

  <template #header>
    <personal-info-header :userinfo="`${myPosition ? myPosition : '--'} / ${rating.at(-1).index} місце у рейтингу`"/>
  </template>

  <template #body>
    <div class="section-name">Рейтинг Учасників</div>
    <br/>
    <div class="rating-section">
      <div v-for="(user, i) in rating" :key="user.position">
        <br v-if='user.position != i+1'>
        <div class="rating-item" :class='{ "rating-item-me": user.position == myPosition }'>
          <div v-if='!user.avatar' class="user-icon material-icons-outlined" :style="{ color: userColors[i] }">account_circle</div>
          <img v-else :src='user.avatar' class='user-avatar'>
          <div class="user-info">
            <div class="user-name">
              <b v-if='user.index>3'>#{{ user.index }}</b>
              <i v-else class="material-icons-outlined" :style='{ color: medalColors[i] }'>workspace_premium</i>
              &nbsp;&thinsp;<span>{{ user._id }}</span>
            </div>
            <div class='user-score'>
              <span>{{ user.score }} балів</span>&thinsp;
              <span :style='{ color: userScoreDiffs[i]>0 ? $green : "#0008", fontWeight: userScoreDiffs[i]>0 ? "bold" : "normal" }'>
                (+{{ userScoreDiffs[i] }})
              </span>
            </div>
          </div>
          <div>
            <i class="material-icons" :style="{ color: userProgressColors[i] }">
              {{ userPositionDiffs[i] > 0 ? "arrow_upward" : userPositionDiffs[i] == 0 ? "remove" : "arrow_downward" }}
            </i>
            <span class='user-position' :style="{ color: userProgressColors[i] }">{{ String(Math.abs(userPositionDiffs[i])) }}</span>
          </div>
        </div>
      </div>
    </div>
  </template>

</main-layout>
</template>

<script>
import mainLayout from '@/layouts/main-layout.vue'
import personalInfoHeader from '@/components/personal-info-header.vue'
export default {
  components: { mainLayout, personalInfoHeader },
    data() { return {
      username: localStorage.getItem('username'),
      medalColors: ['#c9b037', '#b4b4b4', '#ad8a56'],
    }},
  computed: {
    rating() { return this.$store.state.rating },
    myPosition() { return this.rating.find(e => e._id == this.username)?.position },
    userScores() { return this.rating.map(e => e.score) },
    userColors() { return this.userScores.map(e => this.mix3(e / this.userScores[3] * 100)) },
    userScoreDiffs() { return this.rating.map(e => e.score - (e.scorePrev || 0)) },
    userPositionDiffs() { return this.rating.map((e, i) => (e?.indexPrev || this.rating.length) - e.index) },
    userProgressColors() { return this.userPositionDiffs.map(e => e>0 ? this.$green : e==0 ? '#3498DB' : this.$red) },
  }
}
</script>

<style lang="scss" scoped>
//* {border: 1px solid red}
.section-name {
  width: min(100%, 61em);
  margin: auto;
  font-size: 1.15em;
  font-weight: bold;
  padding: 1em 1.5em;
  box-sizing: border-box;
  margin-bottom: .5em;
  border-radius: 4px;
  text-transform: uppercase;
  background: rgba(0,0,0,.025);
}
.rating-section {
  width: min(100%, 70em);
  margin: auto;
  border-radius: 4px;
  box-shadow: 0 2px 16px 5px rgba(0,0,0,.05);
}
.rating-item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  column-gap: 1.5em;
  padding: min(2vmax, 20px) 2.25em;
  background: #fff;
  border-bottom: 1px solid #0001;
  .user-index { 
    width: 2ch;
    text-align: center;
    font-size: 1.25em;
    font-weight: bold;
    font-family: 'Oswald';
  }
  .user-info {
    flex-grow: 1;
    line-height: 1.5em;
    //text-align: right;
  }
  .user-score {
    margin-top: 4px;
    color: #0008;
    font-size: .9em;
  }
  .user-icon {
    font-size: 2.5em !important;
    vertical-align: middle;
  }
  .user-avatar {
    width: 2.5em;
    height: 2.5em;
    border-radius: 50%;
    margin: -.1em 0;
  }
  i {
    margin-right: 2px;
    font-size: 1.5em;
    vertical-align: middle; 
  }
  .user-position {
    //font-size: 1.15em;
    font-weight: bold;
    vertical-align: middle;
  }
}
.rating-item-me {
  font-weight: bold;
}
</style>